import Axios from 'axios';

const getCategory = () => {
    "use strict";
    return Axios
        .get(`${window.apiUrl}/category`, {
            withCredentials: true
        })
        .then((res) => {
            let items = res.data.items;
            let cats = [];
            items.forEach((item) => {
                cats.push(item._id);
            });
            return cats;
        });
};

const findById = (id) => {
    "use strict";
    return Axios
        .get(`${window.apiUrl}/category/${id}`,
            {
                withCredentials: true
            }
        )
        .then((res) => {
            if (res.status == 200) {
                return res.data;
            }
        });
};

const save = (formData) => {
    "use strict";
    if (formData.status) {
        formData.status = 1;
    } else {
        formData.status = 0;
    }
    let id = formData.id;

    if (id !== undefined) {
        // UPDATE
        return Axios
            .post(`${window.apiUrl}/category/${id}`, formData,
                {
                    withCredentials: true
                }
            )
            .then((res) => {
                return res;
            });
    } else {
        // SAVE NEW
        return Axios
            .post(`${window.apiUrl}/category`, formData,
                {
                    withCredentials: true
                }
            )
            .then((res) => {
                return res;
            });
    }

};

export  default  {
    getCategory,
    findById,
    save
}


