<template>
    <div class="form-elements">
        <div class="row" v-if="message.show">
            <div class="col-md-12">
                <vuestic-alert :type="message.type" :withCloseBtn="true">
                    <span class="badge badge-pill badge-success">{{message.title}}</span>
                    {{message.text}}
                    <i class="fa fa-close alert-close"></i>
                </vuestic-alert>
            </div>
        </div>
        <div class="row">
            <div class="col-sm-12">
                <vuestic-widget class=" color-buttons" headerText="">
                    <div class="row">
                        <div class=".col-md-1">
                            <button class="btn btn-secondary focus btn-micro">
                                <div class="btn-with-icon-content"><i class="glyphicon glyphicon-floppy-disk"></i> Save & List</div>
                            </button>
                        </div>
                        &nbsp; &nbsp;
                        <div class=".col-md-1">
                            <button class="btn btn-secondary focus btn-micro" @click="save">
                                <div class="btn-with-icon-content"><i class="glyphicon glyphicon-floppy-disk"></i> Save</div>
                            </button>
                        </div>
                    </div>
                </vuestic-widget>
            </div>
        </div>
        <div class="row">
            <div class="col-md-8">
                <vuestic-widget headerText="Create Category">
                    <form>
                        <div class="row">
                            <div class="col-md-12">
                                <fieldset>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <input id="simple-input" required v-model="formData.name"/>
                                            <label class="control-label" for="simple-input">Name</label><i class="bar"></i>
                                        </div>
                                    </div>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <input id="simple-input" required v-model="formData.slug"/>
                                            <label class="control-label" for="simple-input">Slug</label><i class="bar"></i>
                                        </div>
                                    </div>
                                    <vuestic-simple-select
                                            label="Type"
                                            v-model="formData.type"
                                            v-bind:options="typeOptions">
                                    </vuestic-simple-select>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <textarea type="text" id="simple-textarea" required v-model="formData.description"></textarea>
                                            <label class="control-label" for="simple-textarea">Description</label><i class="bar"></i>
                                        </div>
                                    </div>
                                </fieldset>
                            </div>
                        </div>
                    </form>
                </vuestic-widget>
            </div>
            <div class="col-md-4">
                <vuestic-widget headerText="Extra">
                    <form>
                        <div class="row">
                            <div class="col-md-12">
                                <fieldset>
                                    <vuestic-switch v-model="formData.status">
                                        <span slot="trueTitle">Publish</span>
                                        <span slot="falseTitle">Unpublish</span>
                                    </vuestic-switch>
                                </fieldset>
                            </div>
                        </div>
                    </form>
                </vuestic-widget>
            </div>
        </div>
    </div>
</template>

<script>
    import VuesticSwitch from 'components/vuestic-components/vuestic-switch/VuesticSwitch'
    import VuesticSimpleSelect from 'components/vuestic-components/vuestic-simple-select/VuesticSimpleSelect'
    import VuesticMultiSelect from 'components/vuestic-components/vuestic-multi-select/VuesticMultiSelect'

    import CategoryService from 'services/category';

    export default {
        name: 'form-elements',
        components: {
            VuesticSwitch,
            VuesticSimpleSelect
        },
        computed: {},
        data () {
            return {
                typeOptions: ['product', 'post'],
                formData: {
                    name: '',
                    slug: '',
                    type: 'post',
                    description: '',
                    status: true
                },
                message: {
                    show: false,
                    title: "",
                    type: "success",
                    text: ""
                }
            }
        },
        methods: {
            save(){
                let id = this.$route.params.id;
                let data = this.formData;
                data.id = id;
                CategoryService
                    .save(data)
                    .then(res => {
                        let message = {};
                        if (res.status == 200) {
                            this.$router.push(`/category/${res.data._id}`);
                            // ALERT Message
                            message = {
                                show: true,
                                type: "success",
                                title: "Success",
                                text: "Save category successful"
                            };
                        } else {
                            message = {
                                show: true,
                                type: "danger",
                                title: "Error",
                                text: "Something wrong!"
                            };
                        }

                        this.message = message;
                    })
            }
        },
        mounted () {
            let id = this.$route.params.id;
            if (id) {
                CategoryService
                    .findById(id)
                    .then((res) => {
                            this.formData.name = res.name;
                            this.formData.slug = res.slug;
                            this.formData.type = res.type;
                            this.formData.description = res.description;
                            this.formData.status = res.status ? true : false;
                        }
                    )
            }
            this.$validator.validateAll();
        }
    }
</script>

<style lang="scss">
    .abc-checkbox, .abc-radio {
        display: flex !important;
        justify-content: flex-start;
    }

    input[type=checkbox]:disabled + label, input[type=radio]:disabled + label,
    input[type=checkbox]:disabled, input[type=radio]:disabled {
        cursor: not-allowed;
    }

    .non-padding-left {
        padding-left: 0px;
    }

    .fr-box.fr-basic.fr-top {
        margin-top: 20px;
    }

    .vuestic-switch .vuestic-switch-option.false-option.active {
        background: #ff0000;
        border-bottom: 0.125rem solid #ff0000;
    }
</style>


